
#app {
  font-family: 'Maven Pro', 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  margin-top: 40px;
}
input, button {
  font: inherit;
}
.container {
  max-width: 640px;
}
.qr-code {
  border: solid 1px #D1D1D1;
  border-radius: 5px;
  max-width: 100%;
  padding: 10px;
}
.button-large {
  height: auto;
  padding: 10px 30px;
}
h1 .subtitle {
  font-size: 3rem;
  color: grey;
  display: block;
}



